// storage
import storage from 'good-storage';
import Song from 'common/js/song.js';

const SEARCH_KEY = '__search__';
const MAX_SEARCH_LENGTH = 15;
const FAVORITE_KEY = '__favorite__';
const MAX_FAVORITE_LENGTH = 200;
const RECENT_KEY = '__recent__';
const MAX_RECENT_LENGTH = 100;

// ,,,
function insertArray (arr, val, compare, maxLength) {
    const index = arr.findIndex(compare);
    // ,
    if (index === 0) {
        return;
    }
    // ,
    if (index > 0) {
        // 
        arr.splice(index, 1);
    }
    // 
    arr.unshift(val);
    // 
    if (maxLength && arr.length > maxLength) {
        // pop
        arr.pop();
    }
}

function deleteFromArray (arr, compare) {
    const index = arr.findIndex(compare);
    if (index > -1) {
        arr.splice(index, 1);
    }
}

// 
// query,localStorage,return
export function saveSearch (query) {
    // key default
    let searches = storage.get(SEARCH_KEY, []);
    insertArray(searches, query, (item) => {
        return item === query;
    }, MAX_SEARCH_LENGTH);
    storage.set(SEARCH_KEY, searches);
    return searches;
}

export function loadSearch () {
    return storage.get(SEARCH_KEY, []);
}

export function deleteSearch (query) {
    let searches = storage.get(SEARCH_KEY, []);
    deleteFromArray(searches, (item) => {
        return item === query;
    });
    storage.set(SEARCH_KEY, searches);
    return searches;
}

// 
export function saveFavorite (song) {
    let songs = storage.get(FAVORITE_KEY, []);
    insertArray(songs, song, (item) => {
        return item.id === song.id;
    }, MAX_FAVORITE_LENGTH);
    storage.set(FAVORITE_KEY, songs);
    // Song
    if (songs.length) {
        songs = songs.map((item) => {
            return new Song(item);
        });
    }
    return songs;
}

export function deleteFavorite (song) {
    let songs = storage.get(FAVORITE_KEY, []);
    deleteFromArray(songs, (item) => {
        return item.id === song.id;
    });
    storage.set(FAVORITE_KEY, songs);
    // Song
    if (songs.length) {
        songs = songs.map((item) => {
            return new Song(item);
        });
    }
    return songs;
}

export function loadFavorite () {
    let songs = storage.get(FAVORITE_KEY, []);
    // Song
    if (songs.length) {
        songs = songs.map((item) => {
            return new Song(item);
        });
    }
    return songs;
}

// 
export function saveRecent (song) {
    let songs = storage.get(RECENT_KEY, []);
    insertArray(songs, song, (item) => {
        return item.id === song.id;
    }, MAX_RECENT_LENGTH);
    storage.set(RECENT_KEY, songs);
    // Song
    if (songs.length) {
        songs = songs.map((item) => {
            return new Song(item);
        });
    }
    return songs;
}

export function deleteRecent (song) {
    let songs = storage.get(RECENT_KEY, []);
    deleteFromArray(songs, (item) => {
        return item.id === song.id;
    });
    storage.set(RECENT_KEY, songs);
    // Song
    if (songs.length) {
        songs = songs.map((item) => {
            return new Song(item);
        });
    }
    return songs;
}

export function loadRecent () {
    let songs = storage.get(RECENT_KEY, []);
    // Song
    if (songs.length) {
        songs = songs.map((item) => {
            return new Song(item);
        });
    }
    return songs;
}


